import { Injectable } from '@angular/core';
import * as CryptoJS from 'crypto-js';

const key = CryptoJS.SHA256('dR$&$veFplpEYLpi');
const iv = CryptoJS.MD5('xk!P$@@#NSGxp8&a');
@Injectable({
  providedIn: 'root'
})
export class EncryptDecryptService {

  constructor() { }
  encrypt(value: string) {
    return CryptoJS.AES.encrypt(CryptoJS.enc.Utf8.parse(value), key,
      {
        keySize: 128 / 8,
        iv: iv,
        mode: CryptoJS.mode.CBC,
        padding: CryptoJS.pad.Pkcs7
      }).toString();
  }


  decrypt(value: string | CryptoJS.lib.CipherParams) {
    return CryptoJS.AES.decrypt(value, key, {
      keySize: 128 / 8,
      iv: iv,
      mode: CryptoJS.mode.CBC,
      padding: CryptoJS.pad.Pkcs7
    }).toString(CryptoJS.enc.Utf8);
  }
}
