import { Component, ElementRef, OnInit, ViewChild } from '@angular/core';
import { Router } from '@angular/router';
import { MainService } from 'src/app/main.service';
import { environment } from '../../environments/environment';
import { AppStoreService } from '../appstore.service';
import { HttpService } from '../http.service';

@Component({
  selector: 'app-beneficiary',
  templateUrl: './beneficiary.component.html',
  styleUrls: ['./beneficiary.component.css']
})
export class BeneficiaryComponent implements OnInit {
  @ViewChild('pagination') pag!: ElementRef;
  @ViewChild('snackbar') snackbar!: ElementRef;
  showListing = false;
  showloading = false;

  alertmsg = '';
  alertflag = true;

  browser: any;
  transType = '';

  constructor(
    public main: MainService,
    private http: HttpService,
    private router: Router,
    public appStoreService: AppStoreService
  ) {
    this.getBeneficiaryType();
  }

  ngOnInit(): void {
  }

  getBeneficiaryType() {
    const url = environment.apiurl + 'GetBeneficiaryType';
    const beneficiaryData = {
      userID: 'SIX5184235',
      sessionID: 'S001',
      btoken: 'llll'
    };
    this.http.doPost(url, beneficiaryData).subscribe(
      data => {
        if (data.code === '0000') {
          this.appStoreService.beneficiaryDataList = data.dataList;
        }
      }, error => {
      });

  }

  changeBeneficiary(event: any){
    this.appStoreService.beneficiaryData.value = event.target.value;
  }

  goAddBeneficiary(){
    this.router.navigate(['/main/ibanking/addbeneficiary']);
  }

}
