import { Injectable } from '@angular/core';
import { HttpClient, HttpHeaders, HttpParams } from '@angular/common/http';
import { environment } from '../environments/environment';
import { Observable } from 'rxjs';

@Injectable({
    providedIn: 'root'
})
export class UserSetupListService {

    constructor(private _http: HttpClient) { }

    goSearch(createBody: any, appid: string, domain: string, token: string): Observable<any> {
        return this._http.post(environment.apiurl + 'service001/getuserdatalist?appid=' + appid
        + '&domain=' + domain + '&token=' + token, createBody);
    }

}
