import { NgModule } from '@angular/core';
import { RouterModule, Routes } from '@angular/router';
import { ActivityComponent } from './activity/activity.component';
import { AddBeneficiaryComponent } from './add-beneficiary/add-beneficiary.component';
import { BeneficiaryComponent } from './beneficiary/beneficiary.component';
import { DashboardComponent } from './dashboard/dashboard.component';
import { DetailComponent } from './detail/detail.component';
import { SummaryComponent } from './summary/summary.component';

const routes: Routes = [
  { path: 'main/ibanking/dashboard', component: DashboardComponent },
  { path: 'main/ibanking/details', component: DetailComponent },
  { path: 'main/ibanking/summary', component: SummaryComponent },
  { path: 'main/ibanking/activity', component: ActivityComponent },
  { path: 'main/ibanking/beneficiary', component: BeneficiaryComponent },
  { path: 'main/ibanking/addbeneficiary', component: AddBeneficiaryComponent }
];

@NgModule({
  imports: [RouterModule.forRoot(routes)],
  exports: [RouterModule]
})
export class AppRoutingModule { }
