import { Component, OnInit } from '@angular/core';
import { NgForm } from '@angular/forms';
import { Router } from '@angular/router';
import { MainService } from 'src/app/main.service';
import { CookieService } from 'ngx-cookie-service';
@Component({
  selector: 'app-login',
  templateUrl: './login.component.html',
  styleUrls: ['./login.component.css'],
})
export class LoginComponent implements OnInit {
  atoken: any;

  constructor(
    public main: MainService,
    private _router: Router,
    private cookieService: CookieService
  ) {}

  ngOnInit(): void {}

  onKeyUp(event: any) {}

  loginUser(signInForm: NgForm) {
    console.log('User' + signInForm.value._user);
    console.log('Pass' + signInForm.value._pw);
    const loginData = {
      userid: signInForm.value._user,
      password: signInForm.value._pw,
      appid: this.main.config.appid,
    };
    this.main.doPost('signin', loginData).subscribe((d) => {
      const data: any = {
        atoken:
          'eyJhbGciOiJIUzI1NiJ9.eyJzdWIiOiJ7XCJhcHBpZFwiOlwiaGNtXCIsXCJ1c2VyaWRcIjpcInN5dEBtaXQuY29tLm1tXCJ9IiwiZXhwIjoxNjIxMTc1MTE1LCJpYXQiOjE2MjExNzMzMTV9.26MBbh4mCODtCqfh66s6y13MdJX95MTkZ9MfyD_vPSQ',
        message: 'Successful',
        returncode: '300',
        type: 0,
      };
      if (data != null) {
        if (data.returncode == '300') {
          this.main.profile.userid = signInForm.value._user;
          if (data.type == 0) {
            console.log(JSON.stringify(data));
            this.main.profile.atoken = data.atoken;
            if (
              this.main.config.project_type === 'semi' ||
              this.main.config.project_type === 'full'
            ) {
              this.main.profile.status = true;
              console.log(this.main.config.route_URL);
              const expiredDate = new Date();
              expiredDate.setTime(
                expiredDate.getTime() + 365 * 24 * 60 * 60 * 1000
              );
              this.cookieService.set('atoken', data.atoken, {
                expires: expiredDate,
                sameSite: 'Lax',
              });
              if (this.main.config.domain_type == 'multi') {
                if (this.main.config.skipdomain) {
                  this.getDomain();
                } else {
                  this._router.navigate(['/iam/domain']);
                  // if(this.cookieService.check('domain')){
                  //   this.getDomain();
                  // }else{
                  //   this._router.navigate(['/iam/domain']);
                  // }
                }
              } else {
                this.main.profile._showmenu = true;
                this._router.navigate([this.main.config.route_URL]);
              }
            } else {
              this._router.navigate([this.main.config.product_default_route]);
            }
          } else {
            this.main.profile.type = data.type;
            this.main.profile.session = data.session;
            this.main.profile.code = data.returncode;
            this._router.navigate(['/iam/verify']);
          }
        } else {
          alert(data.message);
        }
      }
    });
  }

  getDomain() {
    const atokenExists: boolean = this.cookieService.check('atoken');
    if (atokenExists) {
      this.atoken = this.cookieService.get('atoken');
    }

    const param = {
      appid: this.main.config.appid,
      userid: this.main.profile.userid,
      atoken: this.atoken,
    };
    this.main.doPost('getdomain', param).subscribe((data) => {
      if (data.returncode == '300') {
        if (data.domain.length == 0) {
          this._router.navigate([this.main.config.route_URL]);
        } else {
          this.main.profile.domain = data.domain;
          if (this.cookieService.check('domain')) {
            this.main.config.domain = this.cookieService.get('domain');
          } else {
            this.main.config.domain = data.domain[0].shortcode;
          }
          this.main.profile._showmenu = true;
          const expiredDate = new Date();
          expiredDate.setTime(
            expiredDate.getTime() + 365 * 24 * 60 * 60 * 1000
          );
          this.cookieService.set('domain', this.main.config.domain, {
            expires: expiredDate,
            sameSite: 'Lax',
          });
          this._router.navigate([this.main.config.route_URL]);
        }
      } else {
      }
    });
  }
}
