import { Injectable } from '@angular/core';
import { HttpClient, HttpHeaders, HttpParams } from '@angular/common/http';
import { environment } from '../environments/environment';
import { Observable } from 'rxjs';

@Injectable({
    providedIn: 'root'
})
export class UserSetupService {

    constructor(private _http: HttpClient) { }

    goSave(createBody: any): Observable<any> {
        return this._http.post(environment.apiurl + 'service001/saveuserdata', createBody);
    }

    goUpdate(createBody: any): Observable<any> {
        return this._http.post(environment.apiurl + 'service001/updateuserdata', createBody);
    }

    goDelete(createBody: any): Observable<any> {
        return this._http.post(environment.apiurl + 'service001/deleteuserdata', createBody);
    }

    getUserData(createBody: any, id: string): Observable<any> {
        return this._http.post(environment.apiurl + 'service001/getuserdatabykey?syskey=' + id, createBody);
    }
}
