import { NgModule } from '@angular/core';
import { RouterModule, Routes } from '@angular/router';
import { MenuSetupComponent } from './menusetup/menusetup.component';
// import { PagerComponent } from './pager/pager.component';
// import { RoleSetupComponent } from './rolesetup/rolesetup.component';
import { UserSetupComponent } from './usersetup/usersetup.component';
import { UserSetupListComponent } from './usersetuplist/usersetuplist.component';

const routes: Routes = [
  {
    path: 'system/user', component: UserSetupComponent
  }, {
    path: 'system/user/userlist', component: UserSetupListComponent
  }
  // , {
  //   path: 'system/role', component: RoleSetupComponent
  // }
  , {
    path: 'system/menu', component: MenuSetupComponent
  }
];

@NgModule({
  imports: [RouterModule.forRoot(routes)],
  exports: [RouterModule]
})
export class AppRoutingModule { }
