import { Component, ElementRef, OnInit, ViewChild } from '@angular/core';
import { Router } from '@angular/router';
import { MainService } from 'src/app/main.service';
import { environment } from '../../environments/environment';
import { AppStoreService } from '../appstore.service';
import { HttpService } from '../http.service';

@Component({
  selector: 'app-add-beneficiary',
  templateUrl: './add-beneficiary.component.html',
  styleUrls: ['./add-beneficiary.component.css']
})
export class AddBeneficiaryComponent implements OnInit {
  @ViewChild('snackbar') snackbar!: ElementRef;
  showListing = false;
  showloading = false;

  alertmsg = '';
  alertflag = true;

  beneficiaryReqData = {
    beneficiaryID: '', accountNo: '', name: '', email: '', beneTypeValue: '', beneTypeName: ''
  };

  beneficiaryResData = {
    beneficiaryRef: '', transDate: ''
  };

  constructor(
    public main: MainService,
    private http: HttpService,
    private router: Router,
    public appStoreService: AppStoreService
  ) { }

  ngOnInit(): void {
  }

  getBeneficiaryName() {
    const item = this.appStoreService.beneficiaryDataList.find(v =>
      v.value === this.appStoreService.beneficiaryData.value);
    if (item) {
      this.beneficiaryReqData.beneTypeName = item.name;
      this.beneficiaryReqData.beneTypeValue = item.value;
      return item.name;
    } else { return ''; }
  }

  goBack() {
    this.router.navigate(['/main/ibanking/beneficiary']);
  }

  goAdd() {
    this.showloading = true;
    const url = environment.apiurl + 'AddBeneficiary';
    const beneficiaryData = {
      btoken: 'aadsfa',
      userID: '959797001028',
      sessionID: 'c8f5b63d504eb826ab770',
      accountNo: this.beneficiaryReqData.accountNo,
      beneTypeValue: this.beneficiaryReqData.beneTypeValue,
      beneTypeName: this.beneficiaryReqData.beneTypeName,
      beneficiaryID: this.beneficiaryReqData.beneficiaryID,
      name: this.beneficiaryReqData.name,
      sKey: '88653951470',
      bankCode: '000',
      bankBranch: '',
      bankName: '',
      beneAddress: '',
      bankAddress1: '',
      bankAddress2: '',
      bankAddress3: '',
      bankAddress4: '',
      email: this.beneficiaryReqData.email,
      phone: ''
    };
    this.http.doPost(url, beneficiaryData).subscribe(
      data => {
        this.showloading = false;
        if (data.code === '0000') {
          alert(data.desc);
        } else {
          alert(data.desc);
        }
      }, error => {
        this.showloading = false;
      });
  }

}
