import { Component, EventEmitter, Input, OnInit, Output } from '@angular/core';

@Component({
  selector: 'app-pager',
  templateUrl: './pager.component.html',
  styleUrls: ['./pager.component.css']
})
export class PagerComponent implements OnInit{

  @Input() rpPageSize!: number;
  @Input() rpPageSizeMax!: number;
  @Input() rpModel!: number;
  @Input() rpCurrentPage!: number;

  @Output() rpChanged: any = new EventEmitter();

  _pages: any[] = [];
  _sizes: any[] = [];

  currentPage = 1;
  prev = 1;
  last = 1;
  next = 2;
  start = 1;
  end = 10;
  pageSize = 10;
  totalCount = 1;

  _flag = false;
  _obj = { current: 1, prev: 1, last: 1, next: 2, start: 1, end: 10, size: 10, totalcount: 1 };

  constructor() {
    if (this.rpPageSize == null || this.rpPageSize === 0) { this.rpPageSize = 10; }
    if (this.rpPageSizeMax == null || this.rpPageSizeMax === 0) { this.rpPageSizeMax = 100; }
    if (this.rpModel == null || this.rpModel < 0) { this.rpModel = 1; }
    if (this.rpCurrentPage == null || this.rpCurrentPage < 0) { this.rpCurrentPage = 1; }
  }

  ngOnInit(): void {
    if (this.rpCurrentPage !== null && this.rpCurrentPage !== undefined) {
      this.currentPage = this.rpCurrentPage;
    }
  }

  ngAfterContentInit(): void {
    this.totalCount = +this.rpModel;
    this.last = Math.ceil(this.totalCount / this.pageSize);

    this.fillPageSizes(this.rpPageSizeMax);
    this.fillPages();
  }

  ngOnChanges(): void {
    this.totalCount = +this.rpModel;

    if (this.rpCurrentPage != null && this.rpCurrentPage > 0) { this.currentPage = + this.rpCurrentPage; }
    else { this.currentPage = 1; }

    if (this.rpPageSize != null && this.rpPageSize > 10) { this.pageSize = + this.rpPageSize; }
    else { this.pageSize = 10; }

    this.end = 10;
    this.last = Math.ceil(this.totalCount / this.pageSize);

    this.fillPages();
    this.updatePager(this.currentPage);
  }

  initializePager(): void {
    this._obj = { current: 1, prev: 1, last: 1, next: 2, start: 1, end: 1, size: 10, totalcount: this.totalCount };
  }

  goFirst(): void {
    this._flag = true;
    this.currentPage = 1;

    this.updatePager(this.currentPage);
  }

  goPrev(): void {
    this._flag = true;
    this.updatePager(this.prev);
  }

  goNext(): void {
    this._flag = true;
    this.updatePager(this.next);
  }

  goLast(): void {
    this._flag = true;
    this.currentPage = Math.ceil(this.totalCount / this.pageSize);

    this.updatePager(this.currentPage);
  }

  updatePager(k: number): void {
    const num = +k;
    this.currentPage = num;

    this.prev = num > 1 ? num - 1 : 1;
    this.next = (num + 1) < this.last ? (num + 1) : this.last;

    this.start = this.totalCount < 1 ? this.totalCount : ((num - 1) * this.pageSize) + 1;
    this.end = (num * this.pageSize) > this.totalCount ? this.totalCount : (num * this.pageSize);

    this._obj =
    {
      current: this.currentPage, prev: this.prev, last: this.last, next: this.next,
      start: this.start, end: this.end, size: this.pageSize, totalcount: this.totalCount
    };

    const data = { obj: this._obj, flag: this._flag };

    this.rpChanged.emit(data);
    this._flag = false;
  }


  changePage(): void {
    this._flag = true;
    this.updatePager(this.currentPage);
  }

  changeRows(): void {
    this.fillPages();
    this.last = Math.ceil(this.totalCount / this.pageSize);

    this.updatePager(1);
  }

  fillPageSizes(pg: number): void {
    const size = +pg;
    this._obj.current = 1;

    const total = size / 10;

    for (let i = 1; i <= total; i++) {
      const s = { value: 0, caption: '' };

      s.value = i * 10;
      s.caption = '' + (i * 10);

      this._sizes.push(s);

      if (s.value === 10) {
        const ss = { value: 0, caption: '' };

        ss.value = 15;
        ss.caption = '' + (15);

        this._sizes.push(ss);
      }
    }
  }

  fillPages(): void {
    this._pages = [];
    this._obj = { current: 1, prev: 1, last: 1, next: 2, start: 1, end: 1, size: 10, totalcount: this.totalCount };

    const num = Math.ceil(this.totalCount / this.pageSize);

    for (let i = 1; i <= num; i++) {
      const p = { value: 0, caption: '' };
      p.value = i;
      p.caption = '' + i;
      this._pages.push(p);
    }
  }

}
