import { Injectable } from '@angular/core';
import { HttpClient, HttpHeaders, HttpParams } from '@angular/common/http';
import { environment } from '../environments/environment';
import { Observable } from 'rxjs';

@Injectable({
    providedIn: 'root'
})
export class ActivityListService {

    constructor(private _http: HttpClient) { }

    getAccountActivity(createBody: any): Observable<any> {
        return this._http.post(environment.apiurl + 'GetAccountActivity', createBody);
    }

    getAccountSummary(createBody: any): Observable<any> {
        return this._http.post(environment.apiurl + 'GetAccountSummary', createBody);
    }

}
