import { Component, ElementRef, OnInit, ViewChild } from '@angular/core';
import { Router } from '@angular/router';
import { MainService } from 'src/app/main.service';
import { DetailListService } from '../detaillist.service';

@Component({
  selector: 'app-detail',
  templateUrl: './detail.component.html',
  styleUrls: ['./detail.component.css']
})
export class DetailComponent implements OnInit {
  @ViewChild('snackbar') snackbar!: ElementRef;
  showListing = false;
  _showloading = false;

  alertmsg = '';
  alertflag = true;

  depositacc = '';
  accSummaryData = this.getAccSummaryDataset();
  accDetailData = this.getAccDetailDataset();

  constructor(
    public main: MainService,
    private detaillistservice: DetailListService,
    private _router: Router
  ) {
    this.depositacc = 'Select';
    this.getSummaryData();
  }

  ngOnInit(): void {
  }

  getAccSummaryDataset() {
    return {
      dataList: [{ acctype: '', avlbal: '', ccy: '', depositacc: '' }], haveAmountTotal: '', oweAmountTotal: ''
    };
  }

  getAccDetailDataset() {
    return {
      accountNo: '', accountName: '', ccy: '', branch: '', status: '', cardNo: ''
    };
  }

  getSummaryData(): void {
    this._showloading = true;
    const summaryData = {
      userID: 'SIX5184235',
      sessionID: 'S001',
      btoken: 'llll'
    };
    this.detaillistservice.getAccountSummary(summaryData).subscribe(
      data => {
        this._showloading = false;
        if (data.code === '0000') {
          this.accSummaryData = data;
          this.accSummaryData.dataList.unshift({ acctype: '', avlbal: '', ccy: '', depositacc: 'Select' });
          this._showloading = false;
        } else {
          alert('Error');
        }
      },
      error => {
        this._showloading = false;
      }
    );
  }

  getAccountDetail(): void {
    if (this.depositacc !== '--Select--') {
      this._showloading = true;
      const detailData = {
        userID: '959797001028',
        sessionID: 'c8f5b63d504eb826ab770',
        accountNo: '0020101100038324',
        btoken: 'aljdf;ajsdf'
      };
      this.detaillistservice.getCheckAccount(detailData).subscribe(
        data => {
          this._showloading = false;
          if (data.code === '0000') {
            this.accDetailData = data;
            this.showListing = true;
            this._showloading = false;
          } else {
            alert('Error');
          }
        },
        error => {
          this._showloading = false;
        }
      );
    }else{
      this.showListing = false;
    }
  }


  showAlertMessage(type: string, msg: string) {
    this.alertmsg = msg;
    this.alertflag = false;
    let snackStyle = 'msg-info';
    if (type === 'success') { snackStyle = 'msg-success'; }
    else if (type === 'warning') { snackStyle = 'msg-warning'; }
    else if (type === 'danger') { snackStyle = 'msg-danger'; }
    else if (type === 'information') { snackStyle = 'msg-info'; }
    this.snackbar.nativeElement.innerHTML = this.alertmsg;
    const snackbar = this.snackbar.nativeElement;
    snackbar.className = 'show ' + snackStyle;
    setTimeout(() => { snackbar.className = snackbar.className.replace('show', ''); }, 3000);
  }

}
