import { Injectable } from '@angular/core';
import { HttpClient, HttpHeaders, HttpParams } from '@angular/common/http';
import { Observable } from 'rxjs';
import { catchError, tap, map, startWith, delay } from 'rxjs/operators';
import { MenuItem } from './model/MenuItem';
import { Domain } from './model/Domain';
import { CookieService } from 'ngx-cookie-service';

@Injectable({
  providedIn: 'any'
})

export class MainService {

  domain: Domain[] = [];
  rightMenu: MenuItem[] = [];
  menu: MenuItem[] = [];

  config = {
    appid: '',
    app: '',
    hosturl: '',
    domain_type: '',
    domain: '',
    project_type: '',
    product_default_route: '',
    domain_flag: false,
    route_URL: '',
    skipdomain: false,
    type : 0
  }

  profile = {
    _showmenu: false,
    domainName: '',
    logo: '',
    status: false,
    userid : '',
    userName: '',
    domain: this.domain,
    menu: this.menu,
    rightMenu: this.rightMenu,
    atoken: '',
    type: '',
    session: '',
    code : ''
  }


  constructor(private _http: HttpClient,private cookieService:CookieService) { }

  createAuthorizationHeader(headers: Headers) {
    const atokenExists: boolean = this.cookieService.check('atoken');
    let atoken: string;
    if(atokenExists){
      atoken = this.cookieService.get('atoken');
      headers.append('Authorization', atoken);
    }
  }

  doGet(url:string,params : any) : Observable<any> {
    const httpParams = new HttpParams({
      fromObject: params
    })
    const atokenExists: boolean = this.cookieService.check('atoken');
    let httpHeaders;
    let atoken: string;
    if(atokenExists){
      atoken = this.cookieService.get('atoken');
       httpHeaders = new HttpHeaders({
        'content-type': 'application/json',
        'Authorization': atoken
      })
    }
    const options = { params: httpParams, headers: httpHeaders };
    return this._http.get<any>(this.config.hosturl + url, options)
      .pipe(
        tap((data: any) =>
          console.log('GET : ' + JSON.stringify(data)))
      );
  }

  doPost(url:String ,createBody: any): Observable<any> {
    const httpOptions = {
      headers: new HttpHeaders({
          'Content-Type': 'application/json'
      })
    };
    return this._http.post(this.config.hosturl + url,createBody,httpOptions).pipe(
      tap((data: any) =>
        console.log('POST : ' + JSON.stringify(data)))
    );;
  }

  getConfig(): Observable<any> {
    return this._http.get('assets/config.json');
  }

}
