import { Component, ElementRef, OnInit, ViewChild } from '@angular/core';
import { Router } from '@angular/router';
import { MainService } from 'src/app/main.service';
import { DashboardService } from '../dashboard.service';

@Component({
  selector: 'app-dashboard',
  templateUrl: './dashboard.component.html',
  styleUrls: ['./dashboard.component.css']
})
export class DashboardComponent implements OnInit {
  @ViewChild('snackbar') snackbar!: ElementRef;
  browser: any;

  showListing = false;
  showloading = false;
  alertmsg = '';
  alertflag = true;

  asset = '0.00';
  liability = '0.00';

  accSummaryData = this.getAccSummaryDataset();
  accSummaryCurrentData = this.getAccSummaryCurrentDataset();

  constructor(
    private dashboardservice: DashboardService,
    public main: MainService,
    private router: Router
  ) {
    // this.getModuleToken();
    this.getSummaryData();
  }

  getAccSummaryDataset() {
    return {
      dataList: [{ acctype: '', avlbal: '', ccy: '', depositacc: '' }], haveAmountTotal: '', oweAmountTotal: ''
    };
  }

  getAccSummaryCurrentDataset() {
    return {
      dataList: [{ acctype: '', avlbal: '', ccy: '', depositacc: '' }]
    };
  }

  ngOnInit(): void {
    this.browser = window;
  }

  // getModuleToken() {
  //   const tokenData = {
  //     appid: this.main.config.appid,
  //     userid: this.main.profile.userid,
  //     domain: 'demo',
  //     atoken: this.main.profile.atoken,
  //     cid: '0000000031'
  //   };
  //   this.dashboardservice.getModuleToken(tokenData).subscribe(
  //     data => {

  //     },
  //     error => { }
  //   );
  // }

  getSummaryData(): void {
    this.showloading = true;
    const summaryData = {
      userID: 'SIX5184235',
      sessionID: 'S001',
      btoken: 'llll'
    };
    this.dashboardservice.getAccountSummary(summaryData).subscribe(
      data => {
        this.showloading = false;
        if (data.code === '0000') {
          this.accSummaryData = data;

          this.accSummaryData.dataList = this.accSummaryData.dataList.map(v =>
            ({ ...v, avlbal: this.browser.readableCurrency(v.avlbal) })
          );
          this.accSummaryCurrentData.dataList = [...this.accSummaryData.dataList];
          this.accSummaryData.dataList = this.accSummaryCurrentData.dataList.filter(f =>
            f.acctype.includes('Savings') || f.acctype.includes('Current'));
          this.accSummaryData.haveAmountTotal = this.browser.
            sum(this.transformShape(this.accSummaryData.dataList, 'avlbal'));

          this.asset = this.accSummaryData.haveAmountTotal;

        } else {
          alert('Error');
        }
      },
      error => {
        this.showloading = false;
      }
    );
  }

  showAlertMessage(type: string, msg: string) {
    this.alertmsg = msg;
    this.alertflag = false;
    let snackStyle = 'msg-info';
    if (type === 'success') { snackStyle = 'msg-success'; }
    else if (type === 'warning') { snackStyle = 'msg-warning'; }
    else if (type === 'danger') { snackStyle = 'msg-danger'; }
    else if (type === 'information') { snackStyle = 'msg-info'; }
    this.snackbar.nativeElement.innerHTML = this.alertmsg;
    const snackbar = this.snackbar.nativeElement;
    snackbar.className = 'show ' + snackStyle;
    setTimeout(() => { snackbar.className = snackbar.className.replace('show', ''); }, 3000);
  }

  transformShape(data: any[], key: string) {
    return data.map(v => v[key]);
  }

}
