import { NgModule } from '@angular/core';
import { RouterModule, Routes } from '@angular/router';
import { BankingSharedModule } from 'projects/banking/src/app/app.module';
import { HCMSharedModule } from 'projects/hcm/src/app/app.module';
import { IAMSharedModule } from 'projects/iam/src/app/app.module';
import { IBankingSharedModule } from 'projects/ibanking/src/app/app.module';
import { AppSystemSharedModule } from 'projects/system/src/app/app.module';
import { AppComponent } from './app.component';
import { NavbarComponent } from './navbar/navbar.component';

const routes: Routes = [
  { path: '', component: AppComponent },
  { path: 'login', loadChildren: '../../projects/iam/src/app/app.module#IAMSharedModule'},
  { path: 'verify', loadChildren: '../../projects/iam/src/app/app.module#IAMSharedModule'},
  { path: 'banking', loadChildren: '../../projects/banking/src/app/app.module#BankingSharedModule'},
  { path: 'system', loadChildren: '../../projects/system/src/app/app.module#AppSystemSharedModule'},
  /*{ path: '**', redirectTo: '/iam/login'},*/
  {
    path: 'main', component: NavbarComponent,
    children: [
      {
        path: 'hcm',
        loadChildren: '../../projects/hcm/src/app/app.module#HCMSharedModule'
      }
    ],
  },
  {
    path: 'main', component: NavbarComponent,
    children: [
      {
        path: 'mit',
        loadChildren: '../../projects/hcm/src/app/app.module#HCMSharedModule'
      }
    ],
  },
  {
    path: 'main', component: NavbarComponent,
    children: [
      {
        path: 'ibanking',
        loadChildren: '../../projects/ibanking/src/app/app.module#IBankingSharedModule'
      }
    ],
  }
];

@NgModule({
  imports: [RouterModule.forRoot(routes),
    IAMSharedModule.forRoot(),
    HCMSharedModule.forRoot(),
    BankingSharedModule.forRoot(),
    AppSystemSharedModule.forRoot(),
    IBankingSharedModule.forRoot()
  ],
  exports: [RouterModule]
})
export class AppRoutingModule { }
