import { Component, ElementRef, OnInit, ViewChild } from '@angular/core';
import { Router, ActivatedRoute } from '@angular/router';

@Component({
  selector: 'app-menusetup',
  templateUrl: './menusetup.component.html',
  styleUrls: ['./menusetup.component.css']
})
export class MenuSetupComponent implements OnInit {
  @ViewChild('snackbar') snackbar!: ElementRef;
  sub: any;
  btnFlag = { delete: true, deleteCursor: 'not-allowed' };
  btnName = 'Save';

  inputFlag = { code: false, codeCursor: '' };

  obj = this.getDefaultObj();

  parentcheck = true;
  childcheck = false;
  mainMenu = true;

  alertmsg = '';
  alertflag = true;

  _showloading = false;

  constructor(
    private _router: Router,
    private route: ActivatedRoute,
  ) {
  }

  ngOnInit(): void {
    this.sub = this.route.params.subscribe((params) => {
      const cmd = params.cmd;
      if (cmd != null && cmd !== '' && cmd === 'new') {
      } else if (cmd != null && cmd !== '' && cmd === 'read') {
        const id = params.id;
        this.goReadByKey(id);
      }
    });
  }

  getDefaultObj() {
    return { syskey: 0, mainMenu: '', code: '', menuLink: '', description: '', menu: [] };
  }

  goReadByKey(id: any) {

  }

  goNew() {
    this.obj = this.getDefaultObj();

    this.parentcheck = true;
    this.childcheck = false;
    this.mainMenu = true;

    this.btnName = 'Save';
    this.btnFlag = { delete: true, deleteCursor: 'not-allowed' };
    this.inputFlag = { code: false, codeCursor: '' };
  }

  goPost() {
    if (this.goValidation()) {

    }
  }

  goDelete() {

  }

  goList() {
    this._router.navigate(['/system/menu/menulist']);
  }


  goValidation() {

    if (this.obj.mainMenu === '' && this.mainMenu === false) {
      this.showAlertMessage('warning', 'Invalid Main Menu!');
      return false;
    }

    if (this.obj.code === '') {
      this.showAlertMessage('warning', 'Invalid Code!');
      return false;
    }

    return true;
  }

  showAlertMessage(type: string, msg: string) {
    this.alertmsg = msg;
    this.alertflag = false;
    let snackStyle = 'msg-info';
    if (type === 'success') { snackStyle = 'msg-success'; }
    else if (type === 'warning') { snackStyle = 'msg-warning'; }
    else if (type === 'danger') { snackStyle = 'msg-danger'; }
    else if (type === 'information') { snackStyle = 'msg-info'; }
    this.snackbar.nativeElement.innerHTML = this.alertmsg;
    const snackbar = this.snackbar.nativeElement;
    snackbar.className = 'show ' + snackStyle;
    setTimeout(() => { snackbar.className = snackbar.className.replace('show', ''); }, 3000);
  }

  checkParent() {
    this.parentcheck = true;
    this.childcheck = false;
    this.mainMenu = true;
  }

  checkChild() {
    this.parentcheck = false;
    this.childcheck = true;
    this.mainMenu = false;
  }

}
