import { Injectable } from '@angular/core';
import { BehaviorSubject } from 'rxjs';
interface BeneData { name: string; value: string; }

interface BeneListItem {
    bankCharges: string;
    merchantCharges: string;
    name: string;
    price: string;
    value: string;
}

@Injectable({
    providedIn: 'root'
})
export class AppStoreService {

    private readonly _beneficiaryData = new BehaviorSubject<BeneData>({ name: '', value: '' });
    private readonly _beneficiaryDataList = new BehaviorSubject<BeneListItem[]>([]);

    readonly beneficiaryData$ = this._beneficiaryData.asObservable();
    readonly beneficiaryDataList$ = this._beneficiaryDataList.asObservable();

    set beneficiaryData(v: BeneData) {
        this._beneficiaryData.next(v);
    }

    get beneficiaryData() {
        return this._beneficiaryData.getValue();
    }

    set beneficiaryDataList(v: BeneListItem[]) {
        this._beneficiaryDataList.next(v);
    }

    get beneficiaryDataList() {
        return this._beneficiaryDataList.getValue();
    }
}
