import { Component, ElementRef, OnInit, ViewChild } from '@angular/core';
import { Router } from '@angular/router';
import { MainService } from 'src/app/main.service';
import { SummaryListService } from '../summarylist.service';

@Component({
  selector: 'app-summary',
  templateUrl: './summary.component.html',
  styleUrls: ['./summary.component.css']
})
export class SummaryComponent implements OnInit {
  @ViewChild('snackbar') snackbar!: ElementRef;
  browser: any;

  showListing = false;
  showloading = false;
  alertmsg = '';
  alertflag = true;

  depositacc = '';

  currency = [
    { key: 0, value: 'EUR', selected: false }, { key: 1, value: 'MMK', selected: true },
    { key: 2, value: 'SGD', selected: false }, { key: 3, value: 'USD', selected: false }
  ];
  currencyData = 'MMK';

  actions = [
    { key: 'det', value: 'Account Details' }, { key: 'act', value: 'Account Activity' }
  ];
  actionData = 'Account Summary';

  savingsAndCurrentAmt = '0.00';
  depositAmt = '0.00';
  loanAmt = '0.00';
  portfolioAmt = '0.00';

  accSummaryData = this.getAccSummaryDataset();
  accSummaryCurrentData = this.getAccSummaryCurrentDataset();

  constructor(
    public main: MainService,
    private summarylistservice: SummaryListService,
    private router: Router
  ) {
    this.getSummaryData();
  }

  ngOnInit(): void {
    this.browser = window;
  }

  getAccSummaryDataset() {
    return {
      dataList: [{ acctype: '', avlbal: '', ccy: '', depositacc: '' }], haveAmountTotal: '', oweAmountTotal: ''
    };
  }

  getAccSummaryCurrentDataset() {
    return {
      dataList: [{ acctype: '', avlbal: '', ccy: '', depositacc: '' }]
    };
  }

  goTo(key: string): void {
    if (key === 'det') { this.router.navigate(['/main/ibanking/details']); }
    else if (key === 'act') { this.router.navigate(['/main/ibanking/activity']); }
  }

  getSummaryData(): void {
    this.showloading = true;
    const summaryData = {
      userID: 'SIX5184235',
      sessionID: 'S001',
      btoken: 'llll'
    };
    this.summarylistservice.getAccountSummary(summaryData).subscribe(
      data => {
        this.showloading = false;
        if (data.code === '0000') {
          this.accSummaryData = data;
          // thousand separater and .00 format
          this.accSummaryData.dataList = this.accSummaryData.dataList.map(v =>
            ({ ...v, avlbal: this.browser.readableCurrency(v.avlbal) })
          );

          // copy origin data to current data
          this.accSummaryCurrentData.dataList = [...this.accSummaryData.dataList];

          // calculate depositAmt
          this.depositAmt = this.browser.sum(this.transformShape(this.accSummaryData.dataList.filter(f =>
            f.acctype.includes('Fixed')), 'avlbal'));

          // savings & current datalist filter / calculate saving & current total amount
          this.accSummaryData.dataList = this.accSummaryCurrentData.dataList.filter(f =>
            f.acctype.includes('Savings') || f.acctype.includes('Current'));

          this.accSummaryData.haveAmountTotal = this.browser.sum(this.transformShape(this.accSummaryData.dataList, 'avlbal'));
          this.savingsAndCurrentAmt = this.accSummaryData.haveAmountTotal;

          // calculate portfolioAmt
          this.portfolioAmt = this.browser.sum([this.savingsAndCurrentAmt, this.depositAmt, '-' + this.loanAmt]);

          this.showListing = true;
        } else {
          this.showListing = false;
          this.showAlertMessage('danger', data.desc);
        }
      },
      error => {
        this.showListing = false;
        this.showloading = false;
        this.showAlertMessage('danger', 'Server Error');
      }
    );
  }


  showAlertMessage(type: string, msg: string) {
    this.alertmsg = msg;
    this.alertflag = false;
    let snackStyle = 'msg-info';
    if (type === 'success') { snackStyle = 'msg-success'; }
    else if (type === 'warning') { snackStyle = 'msg-warning'; }
    else if (type === 'danger') { snackStyle = 'msg-danger'; }
    else if (type === 'information') { snackStyle = 'msg-info'; }
    this.snackbar.nativeElement.innerHTML = this.alertmsg;
    const snackbar = this.snackbar.nativeElement;
    snackbar.className = 'show ' + snackStyle;
    setTimeout(() => { snackbar.className = snackbar.className.replace('show', ''); }, 3000);
  }

  getChangeCurrency(ccy: any) {
    this.accSummaryData.dataList = this.accSummaryCurrentData.dataList.filter(f =>
      f.ccy.includes(ccy.target.value) && (f.acctype.includes('Savings') || f.acctype.includes('Current')));

    this.accSummaryData.haveAmountTotal = this.browser.sum(this.transformShape(this.accSummaryData.dataList, 'avlbal'));
  }

  transformShape(data: any[], key: string) {
    return data.map(v => v[key]);
  }
}
